import React from 'react';
import { Modal, Form, DatePicker, Upload, Button } from 'antd';
import UploadImg from './UploadButton.png';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
function UploadReceiptsForm(props) {
  // Declare a new state variable, which we'll call "count"
  const { getFieldDecorator } = props.form;
  const normFile = e => {
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };

  const onSubmit = e => {
    props.form.validateFields((err, values) => {
      if (!err) {
        let paymentRecipts = values.upload.map(upload => ({
          receivedAt: values.receivedAt,
          name: upload.response.name,
          url: upload.response.path,
        }));
        SuperFetch.post('payment/paymentReceipt', paymentRecipts)
          .then(response => {
            if (response.status === 200) {
              notification('success', 'Receipt uploaded!');
            } else {
              notification('error', 'something goes wrong');
            }
            props.fetchPaymentReceiptsData();
            props.onCancel();
            props.form.resetFields();
          })
          .catch(e => console.log(e));
      }
    });
  };
  return (
    <Modal
      title="Basic Modal"
      visible={props.isUploadVisible}
      footer={[
        <Button key="back" type="primary" size="large" onClick={props.onCancel}>
          Return
        </Button>,
        <Button key="submit" type="primary" size="large" onClick={onSubmit}>
          Submit
        </Button>,
      ]}
    >
      <Form layout="vertical">
        <Form.Item label="Title">
          {getFieldDecorator('receivedAt', {
            rules: [
              {
                required: true,
                message: 'Please input the title of collection!',
              },
            ],
          })(<DatePicker format={'DD MMM YYYY'} />)}
        </Form.Item>
      </Form>
      <Form.Item label="Upload">
        {getFieldDecorator('upload', {
          valuePropName: 'fileList',
          getValueFromEvent: normFile,
          rules: [{ required: true, message: 'Please upload your files!' }],
        })(
          <Upload
            name="logo"
            action="/api/claim/upload"
            headers={{
              Authorization: localStorage.getItem('id_token') || undefined,
            }}
            multiple={false}
          >
            <img src={UploadImg} alt="UploadImg" />
          </Upload>
        )}
      </Form.Item>
      {/* <Upload
        action="/api/claim/upload"
        headers={{
            Authorization: localStorage.getItem('id_token') || undefined,
        }}
        >
        <img src={UploadImg} alt="UploadImg" />
        </Upload> */}
    </Modal>
  );
}

export default Form.create()(UploadReceiptsForm);
